import React, { Component } from 'react';
import {Thumbnail, Grid, Row, Col} from 'react-bootstrap';
import'../App.css';

export default class Portfolio extends Component{
    render(){
      return(
        <div id="portfolioDiv">
          <hr/>
          <Grid>
            <Row>
            <Col md={6} xs={12}>
              <a href="https://spring-vending-machine.herokuapp.com/" target="#" style = {{textDecoration: "none"}}><Thumbnail style={{height:450, cursor:'pointer'}} className="card">
                  <h2 style={{textAlign: "center"}}>Vending Machine</h2>
                    <hr/>
                    <img src="./img/VendingMachineSpringMVC.png" alt="Vending Machine SpringMVC" className={"center responsive"} style={{width:265, height:150}}/>
                    <hr/>
                    <p style={{textAlign: "center"}}>
                      This project explored the use of HTTP calls requiring the customer to click a button to add money and select an item to purchase. On the backend, a Spring container used controller servlets to produce responses.
                    </p>
                </Thumbnail></a>
              </Col>
              <Col md={6} xs={12}>
              <a href="https://minicasemanagementsystem20190225094040.azurewebsites.net" target="#" style = {{textDecoration: "none"}}><Thumbnail style={{height:450, cursor:'pointer'}} className="card">
                  <h2 style={{textAlign: "center"}}> Mini Case Management System</h2>
                    <hr/>
                    <img src="./img/CMS.png" alt="Case Management System" className={"center responsive"} style={{width:265, height:150}}/>
                    <hr/>
                    <p style={{textAlign: "center"}}>
                      This project explores the use of ASP.NET Framework specifically utilizing MVC as the design pattern, Entity Framework as the ORM and SQL Server as the Database and Server. It is a CRUD(Create, Read, Update, Delete) app that allows the user to input case information and have it displayed given route information.
                    </p>
                </Thumbnail></a>
              </Col>
            </Row>
          </Grid>
        </div>
      );
    };
  }
  


