import React, { Component } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll'
import {Navbar} from 'react-bootstrap';
import'../App.css';

export default class NavbarComponent extends Component{
    render(){
      return(
            <Navbar id ="navBar" inverse collapseOnSelect style={{marginBottom:0}} >
              <Navbar.Header>
                <Navbar.Toggle />
              </Navbar.Header>
              <Navbar.Collapse id ="navcollapse" className="pull-right"> 
                    <Navbar.Text >
                      <AnchorLink href='#aboutMeSection' style = {{textDecoration: "none", color:"white"}}>About Me</AnchorLink>
                    </Navbar.Text>
                    <Navbar.Text>
                      <AnchorLink href="#portfolioDiv" style = {{textDecoration: "none", color:"white"}}>Portfolio</AnchorLink>
                    </Navbar.Text>
                    <Navbar.Text>
                      <AnchorLink href="#contactMeDiv" style = {{textDecoration: "none", color:"white"}}>Contact Me</AnchorLink>
                    </Navbar.Text>
                    <Navbar.Text>
                      <a className="blogText" style = {{textDecoration: "none", color:"white"}} href="https://stephjbrown.blogspot.com/" target ="#">Blog</a>
                    </Navbar.Text>
              </Navbar.Collapse>
            </Navbar>
      );
    };
  }


